<template>
  <div class="main-div">
    <div class="main-bg">
      <div class="logo-bg"></div>
      <div class="description-container">
        <p>在南站四周共有9個「站牌」，代表不同的交通路線。進入轉乘陣後會依序出現三道題目，必須要照最佳轉乘方式正確輸入搭乘時段「代碼」，直到抵達目的地。</p>
      </div>
    </div>
    <div id="button-contain"><div id="button" @click="nextPressed"></div></div>
  </div>
</template>

<script>
export default {
  name: 'TransferDiagramIntroduction',
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      }
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', false)
  },
  methods: {
    nextPressed () {
      this.$router.push({name: 'CommuteSouthMainStation'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    min-height: 100vh;
    width: 100%;
    background-color: black;
    position: relative;
    .main-bg {
      @extend centerContainImage;
      position: absolute;
      width: 100%;
      height: 65%;
      top: 7%;
      background-image: url('~@/assets/images/commute/transfer-main-paper-bg.png');
      .logo-bg {
        @extend centerContainImage;
        width: 100%;
        height: 42%;
        background-image: url('~@/assets/images/commute/transfer-diagram-logo.png');
        position: absolute;
        top: 6%;
        right: 3%;
      }
      .description-container {
        width: 100%;
        height: 52%;
        position: absolute;
        bottom: 0%;
      }
      p {
        margin: 3% 15%;
        font-weight: bold;
        line-height: 1.8;
        color: black;
      }
    }

    #button-contain {
      height: 50px;
      width: 100%;
      bottom: 15%;
      text-align: center;
      position: absolute;
      #button {
        display: inline-block;
        width: 50px;
        height: 50px;
        background-image: url('~@/assets/images/go-right-arrow.png');
        background-size: contain;
      }
    }
  }
</style>



